.header {
    padding: 1rem 0REM;
    background-image: url('../../images/background-headeer.svg');
    height: 336px;
    width: 100%;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-wrap: wrap;
}

.header-text {
    position: relative;
    width: 720px;

    font-family: 'Helvetica Compressed';
    font-style: normal;
    font-weight: 500;
    font-size: 40px;
    /* width: 320px; */
    line-height: 68px;
    text-align: center;
    letter-spacing: 0.1em;
    color: #FFFFFF;



}

.header-text span {
    color: #FFFF00;
}

.logo {
    /* image 2 */


}

@media screen and (max-width: 768px) {
    .header {
        height: auto;
    }
}