.search-content .input-container {
    position: relative;
    margin-top: 20px;
    position: relative;
    width: 100%;
    margin-top: 20px;
    justify-content: center;
    display: flex;
    /* Adjust margin as needed */
}

.search-content {
    text-align: center
}

.search-content .input-container .input-label {
    display: flex;
    align-items: center;
    background: #FFFFFF;
    border: 1px solid rgba(145, 142, 142, 0.33);
    border-radius: 30px;
    height: 4rem;
    width: 60%;
}

.search-content .input-container .input-icon {
    margin-left: 1rem;
    /* Adjust margin as needed */
    width: 24px;
    /* Adjust width of the icon */
    height: 24px;
    /* Adjust height of the icon */
}

.search-content .input-container input {
    flex: 1;
    border: none;
    outline: none;
    background: transparent;
    font-family: 'Helvetica';
    font-style: normal;
    font-weight: 400;
    font-size: 20px;
    line-height: 23px;
    padding: 1rem;
    color: #000000;
    /* Adjust text color as needed */
}

.search-content h3 {
    /* Search our database */
    font-family: 'Helvetica';
    font-style: normal;
    font-weight: 700;
    font-size: 32px;
    line-height: 37px;
    text-align: center;
    letter-spacing: 0.05em;
    color: #605A5A;
}